<template>
  <div id="qrcode-box" class="qrcode-box">
    <div id="copy-area" class="copy-area">
      <div class="qrcode-tip"> {{name}}{{$t('inviteYou')}}</div>
      <div class="qrcode-code">
        <div id="qrcodes" ref="qrcode"></div>
        <img :src="img" class="qrcode-logo" />
      </div>
    </div>
    <div :class="{'has-copyed':hasCopy, 'need-copy':!hasCopy}" @click="createQrImg">{{hasCopy?$t('hasCopy'):$t('copyQrcode')}}</div>
  </div>
</template>
<script>
import QRCode from 'qrcodejs2'
import html2canvas from 'html2canvas';
// import {clipboard, nativeImage} from 'electron';
const clipboard = window.require('electron').clipboard;
const nativeImage = window.require('electron').nativeImage;
//const { clipboard, nativeImage } = require('electron')
// import { mapGetters } from 'vuex'
export default {
  props: {
    text: String,
    img: String,
    name:String,
    width: {
      type: Number,
      default: 300
    },
    height: {
      type: Number,
      default: 300
    },
    gender: {
      type: Number,
      default: 0
    },
    name: String,
    isShowDetail: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      qrcode: '',
      hasCopy:false,
    };
  },
  computed: {
    // ...mapGetters(['user_account'])
  },
  mounted() {
    this.$nextTick(()=>{
      this.createQrcode();
    })
  },
  watch: {
    text(value) {
      this.qrcode.makeCode(value)
    }
  },
  components: {
    QRCode
  },
  methods: {
    createQrcode () {
      this.qrcode = new QRCode(this.$refs.qrcode, {
        width: this.width,    // 设置宽度
        height: this.height,  // 设置高度
        text: this.text
      })
    },
    createQrImg(event) {
      event.preventDefault();  
      html2canvas(document.getElementById("copy-area"),{allowTaint: true}).then((canvas)=> {
        //this.$toast.open({ message: '复制成功', type: 'is-info' })
        let qrcodeDataURL = canvas.toDataURL();
        let qrcodeImg = nativeImage.createFromDataURL(qrcodeDataURL)
        clipboard.writeImage(qrcodeImg)
        this.hasCopy = true;
      });
    }
  }
};
</script>
<style lang="scss">
.qrcode-box {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.copy-area{
  width:600px;
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 55px;
}
.qrcode-tip{
 color: rgb(46,61,122);
    /* color: #000; */
 font-size: 18px;
}
.qrcode-code{
    width: 306px;
    height: 306px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 3px #fff;
    margin-top: 40px;
    margin-bottom: 20px;
}
.qrcode-logo {
    margin: auto;
    position: absolute;
    width: 60px;
    height: 60px;
    top: 0px;
    left: 0px;
    bottom: 20px;
    right: 0px;
    background: #fff;
    border: solid 2px #fff;
    border-radius: 3px;
}
.has-copyed {
  margin-bottom: 90px; 
}
.need-copy{
  cursor: pointer;
  text-decoration: underline;  
  margin-bottom: 90px; 
}

.qrcode-server {
  margin-bottom: 20px;
}

.qrcode-box-info {
  position: absolute;
  bottom: 86px;
  top: 72%;
  font-size: 14px;
  text-align: center;
  color: #000;
}
</style>


