import Vue from 'vue'
import Vuei18n from 'vue-i18n'
import LangZh from './zh.js'
import LangEn from './en.js'

Vue.use(Vuei18n)

export const langConfig = {
  locale: 'zh-CN',
  messages: {
    'zh-CN': LangZh,
    'en-US': LangEn
  }
}

export default new Vuei18n(
  langConfig
)



