<template>
    <div class="home-page">
        <qr-code class="qr-code-info" :text='link' :img='icon' :name='name'></qr-code>
    </div>
</template>
<script>
import qrCode from './qrCode.vue'
import UrlParse from 'url-parse';
export default {
  data () {
    return {
      link:'',
      name:'Linkdood',
      icon:require('../assets/dou.png'),
    }
  },
  created(){
      this.$Service.contact.queryExtendedField({parentId:68,dicKey:'onlineBuyLink'},resp=>{
          console.log('在线购买地址:',resp);
          if(resp.EnterpriseDictionarys){
              this.link = resp.EnterpriseDictionarys[0].extend;
          }
      });
      this.$Service.user.getAccountInfo(null,resp=>{
          console.log('在线购买地址:',resp);
           if(resp.thumbAvatar){
               this.icon = resp.thumbAvatar;
           }
           this.name = resp.name;
      });
      const urlParser = new UrlParse(window.location.href, true);
      console.log('urlParse:',urlParser);
      this.link = urlParser.query.url;
      this.name = urlParser.query.name;
      if(urlParser.query.icon){
          this.icon = urlParser.query.icon;
      }     
  },
  components:{
    qrCode
  }
}
</script>
<style lang="scss" scoped>
.home-page{
    height: 600px;
    width: 800px;
    margin: auto;
    .qr-code-info{
      overflow: hidden;
      width: 100%;
      height: 100%;
    }
}
</style>


